@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@700&display=swap);
* {
	margin: 0;
	box-sizing: border-box;
}

:root {
	--green: hsl(113, 94%, 31%);
}

body {
	font-family: "Spartan", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	max-width: 100%;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.nav-bar {
	display: grid;
	height: 3em;
	padding: 3%;
	grid-template-columns: 40% auto;
	color: white;
	background-color: hsl(113, 94%, 31%);
	background-color: var(--green);
}
.nav-bar p {
	text-align: left;
	padding: 5%;
}

.links {
	padding-top: 3%;
}

.links a {
	text-align: right;
	margin-right: 5%;
	color: white;
	display: inline-block;
}

.blog-list {
	padding-top: 10%;
}

.blog-preview {
	border: 1px solid hsl(113, 94%, 31%);
	border: 1px solid var(--green);
	margin: 5%;
	padding: 5%;
}

.blog-preview:hover {
	box-shadow: 0 5px 5px rgb(199, 198, 198);
}
h2 {
	margin-bottom: 3%;
}
.blog-preview a {
	height: 3em;
	color: hsl(113, 94%, 31%);
	color: var(--green);
	text-decoration: none;
	text-align: left;
}

article {
	padding: 3%;
}

article h2,
article p:first-of-type {
	text-align: left;
	color: hsl(113, 94%, 31%);
	color: var(--green);
	padding: 2%;
}

article p:last-of-type {
	text-align: justify;
	padding: 2%;
}

button {
	background-color: hsl(113, 94%, 31%);
	background-color: var(--green);
	display: block;
	color: white;
	border: none;
	margin: 3% auto;
	height: 3em;
	width: 10em;
	border-radius: 5px;
}

button:hover {
	height: 3.2em;
	box-shadow: 5px 10px 10px rgb(199, 198, 198);
}

form {
	margin: 5% auto;
	width: 90%;
	vertical-align: middle;
}
label {
	display: inline-block;
	width: 20%;
	color: hsl(113, 94%, 31%);
	color: var(--green);
	font-size: 1em;
	vertical-align: middle;
}

label:nth-of-type(2) {
	position: relative;
	top: -3.5em;
}

input {
	text-indent: 5%;
	height: 3em;
	width: 65%;
	margin: 3%;
}

select {
	width: 30%;
	padding: 2%;
}

textarea {
	padding: 1%;
	width: 65%;
	height: 10em;
	margin: 2% 5%;
}

input,
select,
textarea {
	font-family: "Spartan", sans-serif;
	border: 1px solid hsl(113, 94%, 31%);
	border: 1px solid var(--green);
	border-radius: 3px;
}

@media screen and (min-width: 600px) {
	.nav-bar {
		height: 4em;
		grid-template-columns: 50% auto;
	}

	.blog-list {
		padding: 5% 10%;
	}
}

@media screen and (min-width: 600px) {
	.nav-bar {
		padding: 1%;
	}
	.links {
		padding-top: 5%;
	}

	form {
		padding: 2% 5%;
	}
}

@media screen and (min-width: 992px) {
	.nav-bar {
		padding: 1%;
		margin: 0 10% 0%;
	}
	.blog-list {
		padding: 3% 20%;
	}
	h2 {
		margin-bottom: 1%;
	}
	article {
		padding: 5% 15%;
	}
	form {
		margin: 2% auto;
		padding: 2% 10%;
	}
}

@media screen and (min-width: 1440px) {
	body {
		margin: 0 auto;
		max-width: 1440px;
	}
	.nav-bar p,
	.links {
		padding-top: 2%;
	}
	article {
		padding: 2% 20%;
	}
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

